:root {
	--clr-ship: #00aeff;
	--clr-hit: #ff2600;
	--clr-game-over: #77dd77;
	--clr-invalid-click: #ff8800;
	--clr-white: #fafafa;
	--clr-light-grey: #bbbbbe;
	--clr-grey: #707070;
	--clr-dark-grey: #ffffff33;
	--ff-primary: "Russo One", sans-serif;
	--ff-accent: "Press Start 2P", cursive;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

body {
	font-family: var(--ff-primary);
	font-weight: 400;
	font-size: 1rem;
	margin-bottom: 1em;
	background-color: var(--clr-white);
}

body,
h1,
h2,
h3 {
	margin: 0;
}

h1,
h2,
h3 {
	font-family: var(--ff-accent);
	font-weight: 900;
	line-height: 1;
}

.text-center {
	text-align: center;
}

header,
section {
	padding: 3rem 0;
}

.container {
	margin-inline: auto;
}

.split {
	display: flex;
	flex-direction: row;
}

.split > * {
	flex-basis: 100%;
}

.split-child-container {
	position: relative;
	margin: 0;
}

.split-child-container > h3 {
	left: 32px;
	position: inherit;
	bottom: 1em;
}

button {
	background: var(--clr-light-grey);
	box-shadow: inset 0 0 0 99999px var(--clr-dark-grey);
	border: 1px solid var(--clr-grey);
	height: 34px;
	margin-right: -1px;
	margin-top: -1px;
	width: 34px;
}

button:active {
	outline: none;
	border: 1px solid var(--clr-grey);
}

button:visited {
	outline: none;
	border: 1px solid var(--clr-grey);
}

button:focus {
	outline-offset: 0;
	outline: none;
	border: 1px solid var(--clr-grey);
}

button:focus.hit {
	outline: none;
	border: 3px solid var(--clr-ship);
}

button:focus.miss {
	outline: none;
	border: 3px solid var(--clr-hit);
}

button:hover {
	box-shadow: none;
}

.hit {
	background-color: var(--clr-hit);
	outline: none;
	border: 3px solid var(--clr-ship);
}

.miss {
	outline: none;
	border: 3px solid var(--clr-hit);
}

.ship {
	background-color: var(--clr-ship);
}

.reset-button {
	margin: 0 auto;
	display: block;
	width: fit-content;
	background-color: var(--clr-game-over);
}

.grid-row {
	clear: both;
	content: "";
	display: flex;
}

.grid {
	left: 20px;
	position: relative;
	display: table;
}

.invalid-click {
	border: 5px solid var(--clr-invalid-click);
}

.column-label {
	position: relative;
	display: flex;
	width: 304px;
	justify-content: space-between;
	left: 18px;
}

.row-label {
	top: 15px;
	position: absolute;
	display: flex;
	justify-content: space-evenly;
	height: 340px;
	flex-direction: column;
	align-self: flex-start;
	text-align: right;
}

.board {
	flex: 1;
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	position: relative;
}

.hide-board {
	visibility: hidden;
}

.unclickable-board {
	pointer-events: none;
}
